import { Component, OnInit } from '@angular/core';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { Platform } from '@ionic/angular';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
    
    
    public user:any;
    public isIos:boolean;
    
    constructor(public authService: AuthenticationService, private iab: InAppBrowser, public platform:Platform) { 
        this.user = {username:"", password:"", error:"", loading:false};
        
        
        this.isIos = true; //this.platform.is('ios');
        
        
    }

    ngOnInit() {
    }


  
    public login(){
        this.user.loading = true;
        this.user.error = "";
        this.authService.login(this.user).then((token) => {
            this.user.username = "";
            this.user.password = "";
            this.user.loading = false;
        }).catch((e) => {
        console.log(e);
            this.user.loading = false;
            if (e && (e.status === 401 || e.status === 403)){
                this.user.error = "Incorrect email or password";
            }
            else if (e && e.status === 422){
                for (var index in e.error.parameterErrors){
                    let error = e.error.parameterErrors[index];
                    this.user.error = this.user.error + error + " ";
                }
            }
            else if (e && e.status === 0){
                this.user.error = "Your are offline. Connect to the internet and try again.";
            }
            else if (e && (e.status === 500 || e.status === 404)){
                this.user.error = "There was an error on the server. If problems persist, contact us at info@datanow.co.nz.";
            }
            else{
                this.user.error = e.message;
            }   

                      
        })
    }
    
    public signup(){
        //this.navCtrl.push(SignupPage);
    }
    
    
    public openLink(link){
        this.iab.create(link, '_blank', {location:"yes"});
    }

}
